import useAxios from "@/Hooks/useAxios";
import React, { createContext, useContext } from "react";
import { authContext } from "../AuthContext/AuthContext";

export const CartContex = createContext({});
export default function CartContext() {
  const { token } = useContext(authContext);
  const {
    axiosConfig: axiosConfigAdd,
    setAxios: setAxiosAdd
  } = useAxios({
    run: false,
    url: `https://backend-three-nu-89.vercel.app/cart/${product._id}/${quantity}`,
    method: "POST",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    toastLoading: "جاري اضافة العنصر الي عربة التسوق",
    toastSuccess: " تم اضافته بنجاح",
  });
  async function addProductToCart(productId:string,quantity:number) {
    setAxiosAdd({
      ...axiosConfigAdd,
      url: `https://backend-three-nu-89.vercel.app/cart/${productId}/${quantity}`,
      run: true,
    });
  }
  return (
    <CartContex.Provider value={{ addProductToCart }}>
      cartContext
    </CartContex.Provider>
  );
}

